import times from 'lodash.times'
import './index.css';

const cssFilter = (filters) => Object.keys(filters)
  .map(prop => `${prop}(${filters[prop]})`)
  .join(' ');
const px = v => `${v}px`;
const percent = v => `${v}%`;
const deg = v => `${v}deg`;

function run() {
  const canvas = document.getElementById('root');
  const noiseDiv = document.getElementById('noise');
  const music = document.getElementById('music');
  const titles = times(4, n => `jmlpartyhard2Artboard${n + 1}.png`)
  const gifs = times(46, n => `jmlpartyhard2partyhard${n < 10 ? '0' + n : n}.gif`)
    .sort(() => Math.random() - 0.5);
  const changeInterval = 60 / 128 * 4;

  function drawFrame() {
    const time = music.currentTime;
    const pos = Math.floor(time / changeInterval);
    const title = gifs[pos % gifs.length];
    const coef = (time % changeInterval) / changeInterval;
    const coef2 = coef < 0.5 ? 0 : 1;

    canvas.style.backgroundImage = pos < 8
      ? `url(${titles[Math.floor(pos / 2)]})`
      : `url(${gifs[pos % gifs.length]})`;
    canvas.style.filter = cssFilter({
      'hue-rotate': pos < 8
        ? deg(time * 30)
        : deg(Math.random() * 360),
    });
    canvas.style.transform = `scale(${2 - coef2}, ${2 - coef2})`;
    noiseDiv.style.backgroundPositionX = percent(Math.random() * 100);
    noiseDiv.style.backgroundPositionY = percent(Math.random() * 100);
    noiseDiv.style.opacity = Math.random() * 0.05;
    requestAnimationFrame(drawFrame);
  }

  music.play();
  drawFrame();
}

run();


